.site-section-cover {
  position: relative;
  z-index: 0;
  .container {
    position:absolute !important;
    z-index: 2;
  }
    .wrapper {
      width: 100%;
      height: 100vh;
      overflow: hidden;
      position: relative;
      background: linear-gradient(90deg, rgba(42,30,18,1) 0%, rgba(94,69,44,1) 49%, rgba(228,122,16,1) 100%);
    }
    .wrapper>div {
      width: 100%;
      height: 100%;
      position: absolute;
      animation: animate 42s infinite;
      // animation: animate 36s infinite;
      opacity: 0;
      -webkit-background-size: cover;
      background-size: cover;
      background-position: center center;
    }
    
    .wrapper>div:nth-child(2) {
      animation-delay: 6s;
    }
    .wrapper>div:nth-child(3) {
      animation-delay: 12s;
    }
    .wrapper>div:nth-child(4) {
      animation-delay: 18s;
    }
    .wrapper>div:nth-child(5) {
      animation-delay: 24s;
    }
    .wrapper>div:nth-child(6) {
      animation-delay: 30s;
    }
    .wrapper>div:nth-child(7) {
      animation-delay: 36s;
    }
    @keyframes animate {
      10% {
        opacity: 1;
      }
      20% {
        opacity: 1;
      }
      30% {
        opacity: 0;
      }
      40% {
        transform: scale(1.2);
      }
    }
  &,
  .container {
    position: relative;
    z-index: 2;
  }
  &,
  .container>.row {
    height: calc(100vh - 196px);
    min-height: 100vh;
    // min-height: 677px;
    @include media-breakpoint-down(md) {
      height: calc(70vh - 196px);
      min-height: 600px;
    }
  }
  &.overlay {
    position: relative;
    min-height: 100vh;
    &:before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba($black, .6);
      z-index: 1;
    }
  }
  &.inner-page {
    &,
    .container>.row {
      height: auto;
      min-height: auto;
      padding: 2em 0;
    }
  }
  &.img-bg-section {
    background-size: cover;
    background-repeat: no-repeat;
  }
  h1 {
    font-size: 3rem;
    color: $white;
    line-height: 1;
    @include media-breakpoint-down(md) {
      font-size: 2rem;
    }
  }
  p {
    font-size: 18px;
    color: $white; // max-width: 400px;
  }
}

